nav {
  height: 50px;
  width: 100%;
  display: flex;
  flex-direction: row;
  background-color: #333333;
  color: #d5f2f8;
  position: fixed;
  margin-bottom: 100px;
  border-bottom: #fbb635 solid 1px; }
  nav a {
    text-decoration: none;
    padding: 5px 8px;
    color: #d5f2f8; }
  nav .leftSide {
    height: 100%;
    width: 10%;
    display: flex;
    justify-content: center;
    align-items: center; }
    nav .leftSide .iconA {
      height: 100%; }
  nav .rightSide {
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: flex-end; }
    nav .rightSide .navMenuDiv {
      display: flex;
      flex-direction: row;
      width: 250px;
      justify-content: space-evenly; }
      nav .rightSide .navMenuDiv .myStory {
        transition: ease-out all 0.2s; }
        nav .rightSide .navMenuDiv .myStory:hover {
          transition: ease-out all 0.2s;
          box-shadow: inset 1px 1px 5px black; }
      nav .rightSide .navMenuDiv .work {
        transition: ease-out all 0.2s; }
        nav .rightSide .navMenuDiv .work:hover {
          transition: ease-out all 0.2s;
          box-shadow: inset 1px 1px 5px black; }
      nav .rightSide .navMenuDiv .contact {
        transition: ease-out all 0.2s; }
        nav .rightSide .navMenuDiv .contact:hover {
          transition: ease-out all 0.2s;
          box-shadow: inset 1px 1px 5px black; }
  @media (max-width: 600px) {
    nav .rightSide {
      width: 85%; }
    nav .leftSide {
      width: 15%; } }
