@font-face {
  font-family: "Museo Sans";
  src: url("../fonts/museoSans.ttf") format("TrueType"); }

html, #__next, body {
  height: 100%;
  font-family: "Museo Sans";
  cursor: default; }

.motherDiv {
  display: flex;
