  background: #02536D;
  height: 100%; }
  .motherDiv .mainDiv {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    margin-top: 52px; }
    .motherDiv .mainDiv .introSegment {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      padding-left: 10%;
      padding-right: 10%;
      background: #019ED3; }
      .motherDiv .mainDiv .introSegment .introP {
        width: inherit;
