        align-self: center;
        color: #383838;
        font-weight: 900; }
        .motherDiv .mainDiv .introSegment .introP::before {
          content: 'My name is ';
          color: #ffffff; }
        .motherDiv .mainDiv .introSegment .introP::after {
          content: ', and I like to code.';
          color: #ffffff; }
